.note {
  display: block;
  margin-left: 34%;
  padding: 10px 0;
  cursor: pointer;
}

.note-title {
  font-weight: 800;
  font-size: 19px;
  font-family: "Times New Roman", Times, serif;
  padding: 2px 0;
}
.note-body {
  width: 450px;
  display: block;
  word-wrap: break-word;
  margin-bottom: 10px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.delete-button {
  border: none;
  outline: none;
  padding: 2px 10px;
  color: #fff;
  background-color: #ee0505;
  cursor: pointer;
  border-radius: 4px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 17px;
  margin-bottom: 10px;
}
