* {
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
h2 {
  margin-top: 5px;
  color: #0922f3;
  font-weight: 800;
  font-family: Tahoma;
}
h2 span {
  font-weight: 100;
  font-family: cursive;
  letter-spacing: 5px;
  color: #585858;
  text-decoration: underline;
}

.toggle-button {
  border: none;
  outline: none;
  padding: 9px 15px;
  color: #fff;
  background-color: #0922f3;
  cursor: pointer;
  border-radius: 4px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 17px;
  margin: 30px 0;
}
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
input {
  margin: 20px 0;
  height: 20px;
  border: none;
  border-bottom: 2px solid #585858;
  outline: none;
  padding: 5px 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}
textarea {
  outline: none;
  padding: 3px 7px;
  font-size: 14px;
  font-family: cursive;
}
.submit-button {
  border: none;
  outline: none;
  padding: 5px 15px;
  color: #fff;
  background-color: #0922f3;
  cursor: pointer;
  border-radius: 4px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 17px;
  margin-top: 10px;
}
.error {
  color: #9f0101;
  text-align: center;
}
